<script>
import draggable from "vuedraggable";
import Prism from "vue-prism-component";
import "prismjs";
import "prismjs/themes/prism-tomorrow.css";
import Ripple from "vue-ripple-directive";
import axios from "axios";
import ToastificationContent from "@core/components/toastification/ToastificationContent.vue";
// eslint-disable-next-line
import "quill/dist/quill.core.css";
// eslint-disable-next-line
import "quill/dist/quill.snow.css";
// eslint-disable-next-line
import "quill/dist/quill.bubble.css";

import { quillEditor } from "vue-quill-editor";

export default {
  components: {
    draggable,
    Prism,
    ToastificationContent,
    quillEditor,
  },

  data() {
    return {
      newList: [],
      blockedList: [],
      progressList: [],
      completeList: [],
      cancelList: [],
      todos: [],
      todo_title: "",
      todo_description: "",
      todo_status: "",
      todo_priority: "",
      todo_milestone: "",
      todo_author: "",
      todo_assignee: "",
      todo_due_date: "",
      todo_start_date: "",
      snowOption: {
        modules: {
          toolbar: [
            [{ header: [1, 2, false] }],
            ["bold", "italic", "underline", "strike"],
            ["blockquote", "code-block"],
            [{ list: "ordered" }, { list: "bullet" }],
            [{ script: "sub" }, { script: "super" }],
            [{ indent: "-1" }, { indent: "+1" }],
            [{ direction: "rtl" }],
            [{ size: ["small", false, "large", "huge"] }],
            [{ header: [1, 2, 3, 4, 5, 6, false] }],
            [{ color: [] }, { background: [] }],
            [{ font: [] }],
            [{ align: [] }],
            ["clean"],
            ["link", "image", "video"],
          ],
        },
        placeholder: "Type for description",
        theme: "snow",
      },
      statusOptions: [
        { value: 1, text: "New" },
        { value: 2, text: "Blocked" },
        { value: 3, text: "In Progress" },
        { value: 4, text: "Complete" },
        { value: 5, text: "Cancel" },
      ],
      priorityOptions: [
        { value: "low", text: "Low" },
        { value: "medium", text: "Medium" },
        { value: "high", text: "High" },
      ],
      milestoneOptions: [
        { value: 1, text: "Milestone 1" },
        { value: 2, text: "Milestone 2" },
        { value: 3, text: "Milestone 3" },
      ],
      assigneeOptions: [
        { value: 1, text: "User 1" },
        { value: 2, text: "User 2" },
        { value: 3, text: "User 3" },
      ],
    };
  },

  directives: {
    Ripple,
  },

  mounted() {
    axios({
      method: "get",
      url: "https://sw-api.hracademy.id/api/project",
      headers: {
        Authorization: "Bearer " + localStorage.getItem("sw_token"),
      },
    }).then((res) => {
      // add data to todos
      this.todos = res.data.result.data;

      // foreach res data
      res.data.result.data.forEach((element) => {
        // if status == 1
        if (element.status == 1) {
          this.newList.push(element);
        }
        // if status == 2
        if (element.status == 2) {
          this.blockedList.push(element);
        }
        // if status == 3
        if (element.status == 3) {
          this.progressList.push(element);
        }
        // if status == 4
        if (element.status == 4) {
          this.completeList.push(element);
        }
        // if status == 5
        if (element.status == 5) {
          this.cancelList.push(element);
        }
      });
    });
  },

  methods: {
    confirmDelete(id) {
      this.$bvModal
        .msgBoxConfirm("Please confirm that you want to delete everything.", {
          title: "Please Confirm",
          size: "sm",
          okVariant: "primary",
          okTitle: "Yes",
          cancelTitle: "No",
          cancelVariant: "outline-secondary",
          hideHeaderClose: false,
          centered: true,
        })
        .then((value) => {
          if (value) {
            // delete todo
            axios({
              method: "delete",
              url: "https://sw-api.hracademy.id/api/project/" + id,
              headers: {
                Authorization: "Bearer " + localStorage.getItem("sw_token"),
              },
            }).then((res) => {
              this.$toast({
                component: ToastificationContent,
                props: {
                  title: "Notification",
                  icon: "BellIcon",
                  text: "Task has been Deleted",
                  variant: "success",
                },
              });

              this.get_todo();

              this.$bvModal.hide("detail_todo_" + id);
            });
          }
        });
    },

    get_todo() {
      axios({
        method: "get",
        url: "https://sw-api.hracademy.id/api/project",
        headers: {
          Authorization: "Bearer " + localStorage.getItem("sw_token"),
        },
      }).then((res) => {
        // reset newList
        this.newList = [];
        // reset blockedList
        this.blockedList = [];
        // reset progressList
        this.progressList = [];
        // reset completeList
        this.completeList = [];
        // reset cancelList
        this.cancelList = [];
        // reset todos
        this.todos = res.data.result.data;

        // foreach res data
        res.data.result.data.forEach((element) => {
          // if status == 1
          if (element.status == 1) {
            this.newList.push(element);
          }
          // if status == 2
          if (element.status == 2) {
            this.blockedList.push(element);
          }
          // if status == 3
          if (element.status == 3) {
            this.progressList.push(element);
          }
          // if status == 4
          if (element.status == 4) {
            this.completeList.push(element);
          }
          // if status == 5
          if (element.status == 5) {
            this.cancelList.push(element);
          }
        });
      });
    },

    dateTimeNow() {
      var today = new Date();
      var date =
        today.getFullYear() +
        "-" +
        (today.getMonth() + 1) +
        "-" +
        today.getDate();
      var time =
        today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds();
      var dateTime = date + " " + time;
      return dateTime;
    },

    onDragNew(event) {
      if (event.added) {
        axios({
          method: "post",
          url:
            "https://sw-api.hracademy.id/api/project/update/" +
            event.added.element.id,
          headers: {
            Authorization: "Bearer " + localStorage.getItem("sw_token"),
          },
          data: {
            name: event.added.element.name,
            description: event.added.element.description,
            priority: event.added.element.priority,
            date_added: event.added.element.date_added,
            start_date: event.added.element.start_date,
            due_date: event.added.element.due_date,
            date_completed: event.added.element.date_completed,
            project_id: event.added.element.project_id,
            assigned_to: event.added.element.assigned_to,
            created_by: event.added.element.created_by,
            status: 1,
          },
        }).then((res) => {
          this.$toast({
            component: ToastificationContent,
            props: {
              title: "Notification",
              icon: "BellIcon",
              text: "Task has been moved to New",
              variant: "success",
            },
          });
        });
      }
    },

    onDragBlocked(event) {
      if (event.added) {
        axios({
          method: "post",
          url:
            "https://sw-api.hracademy.id/api/project/update/" +
            event.added.element.id,
          headers: {
            Authorization: "Bearer " + localStorage.getItem("sw_token"),
          },
          data: {
            name: event.added.element.name,
            description: event.added.element.description,
            priority: event.added.element.priority,
            date_added: event.added.element.date_added,
            start_date: event.added.element.start_date,
            due_date: event.added.element.due_date,
            date_completed: event.added.element.date_completed,
            project_id: event.added.element.project_id,
            assigned_to: event.added.element.assigned_to,
            created_by: event.added.element.created_by,
            update_at: this.dateTimeNow(),
            status: 2,
          },
        }).then((res) => {
          this.$toast({
            component: ToastificationContent,
            props: {
              title: "Notification",
              icon: "BellIcon",
              text: "Task has been moved to Blocked",
              variant: "success",
            },
          });
        });
      }
    },

    onDragProgress(event) {
      if (event.added) {
        axios({
          method: "post",
          url:
            "https://sw-api.hracademy.id/api/project/update/" +
            event.added.element.id,
          headers: {
            Authorization: "Bearer " + localStorage.getItem("sw_token"),
          },
          data: {
            name: event.added.element.name,
            description: event.added.element.description,
            priority: event.added.element.priority,
            date_added: event.added.element.date_added,
            start_date: event.added.element.start_date,
            due_date: event.added.element.due_date,
            date_completed: event.added.element.date_completed,
            project_id: event.added.element.project_id,
            assigned_to: event.added.element.assigned_to,
            created_by: event.added.element.created_by,
            update_at: this.dateTimeNow(),
            status: 3,
          },
        }).then((res) => {
          this.$toast({
            component: ToastificationContent,
            props: {
              title: "Notification",
              icon: "BellIcon",
              text: "Task has been moved to Progress",
              variant: "success",
            },
          });
        });
      }
    },

    onDragComplete(event) {
      if (event.added) {
        axios({
          method: "post",
          url:
            "https://sw-api.hracademy.id/api/project/update/" +
            event.added.element.id,
          headers: {
            Authorization: "Bearer " + localStorage.getItem("sw_token"),
          },
          data: {
            name: event.added.element.name,
            description: event.added.element.description,
            priority: event.added.element.priority,
            date_added: event.added.element.date_added,
            start_date: event.added.element.start_date,
            due_date: event.added.element.due_date,
            date_completed: event.added.element.date_completed,
            project_id: event.added.element.project_id,
            assigned_to: event.added.element.assigned_to,
            created_by: event.added.element.created_by,
            update_at: this.dateTimeNow(),
            status: 4,
          },
        }).then((res) => {
          this.$toast({
            component: ToastificationContent,
            props: {
              title: "Notification",
              icon: "BellIcon",
              text: "Task has been moved to Complete",
              variant: "success",
            },
          });
        });
      }
    },

    onDragCancel(event) {
      if (event.added) {
        axios({
          method: "post",
          url:
            "https://sw-api.hracademy.id/api/project/update/" +
            event.added.element.id,
          headers: {
            Authorization: "Bearer " + localStorage.getItem("sw_token"),
          },
          data: {
            name: event.added.element.name,
            description: event.added.element.description,
            priority: event.added.element.priority,
            date_added: event.added.element.date_added,
            start_date: event.added.element.start_date,
            due_date: event.added.element.due_date,
            date_completed: event.added.element.date_completed,
            project_id: event.added.element.project_id,
            assigned_to: event.added.element.assigned_to,
            created_by: event.added.element.created_by,
            update_at: this.dateTimeNow(),
            status: 5,
          },
        }).then((res) => {
          this.$toast({
            component: ToastificationContent,
            props: {
              title: "Notification",
              icon: "BellIcon",
              text: "Task has been moved to Cancel",
              variant: "success",
            },
          });
        });
      }
    },

    update_priority(event, data) {
      axios({
        method: "post",
        url: "https://sw-api.hracademy.id/api/project/update/" + data.id,
        headers: {
          Authorization: "Bearer " + localStorage.getItem("sw_token"),
        },
        data: {
          name: data.name,
          description: data.description,
          priority: event.target.value,
          date_added: data.date_added,
          start_date: data.start_date,
          due_date: data.due_date,
          date_completed: data.date_completed,
          project_id: data.project_id,
          assigned_to: data.assigned_to,
          created_by: data.created_by,
          update_at: this.dateTimeNow(),
          status: data.status,
        },
      }).then((res) => {
        this.$toast({
          component: ToastificationContent,
          props: {
            title: "Notification",
            icon: "BellIcon",
            text: "Task Priority has been updated",
            variant: "success",
          },
        });
      });
    },

    add_task() {
      axios({
        method: "post",
        url: "https://sw-api.hracademy.id/api/project",
        headers: {
          Authorization: "Bearer " + localStorage.getItem("sw_token"),
        },
        data: {
          name: this.todo_title,
          description: this.todo_description,
          priority: this.todo_priority,
          start_date: this.todo_start_date,
          due_date: this.todo_due_date,
          date_completed: this.todo_date_completed,
          project_id: this.todo_project_id,
          assigned_to: this.todo_assignee,
          created_by: this.todo_author,
          update_at: this.dateTimeNow(),
          status: 1,
        },
      }).then((res) => {
        this.$toast({
          component: ToastificationContent,
          props: {
            title: "Notification",
            icon: "BellIcon",
            text: "Task has been added",
            variant: "success",
          },
        });
        this.todo_title = "";
        this.todo_description = "";
        this.todo_priority = "";
        this.todo_start_date = "";
        this.todo_due_date = "";
        this.todo_date_completed = "";
        this.todo_project_id = "";
        this.todo_assigned_to = "";
        this.todo_created_by = "";
        this.get_todo();

        // hide b-modal id add_task
        this.$bvModal.hide("add_task");
      });
    },

    update_todo(todo) {
      axios({
        method: "post",
        url: "https://sw-api.hracademy.id/api/project/update/" + todo.id,
        headers: {
          Authorization: "Bearer " + localStorage.getItem("sw_token"),
        },
        data: {
          name: todo.name,
          description: todo.description,
          priority: todo.priority,
          start_date: todo.start_date,
          due_date: todo.due_date,
          date_completed: todo.date_completed,
          project_id: todo.project_id,
          assigned_to: todo.assigned_to,
          created_by: todo.created_by,
          update_at: this.dateTimeNow(),
          status: todo.status,
        },
      }).then((res) => {
        this.$toast({
          component: ToastificationContent,
          props: {
            title: "Notification",
            icon: "BellIcon",
            text: "Task has been updated",
            variant: "success",
          },
        });
        this.get_todo();

        // hide b-modal id add_task
        this.$bvModal.hide("detail_todo_" + todo.id);
      });
    },
  },
};
</script>

